    animation: App-logo-spin infinite 20s linear;
  }
}*/

.app_body {
  background-color: #000000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: start;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: Gabarito, sans-serif;
}

.page_header{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 4vh 5vw;
}

.logo{
    width: 320px;
}

.download_text{
    font-family: Gabarito, sans-serif;
    font-size: 20px;
    margin-right: 20px;
}

.download_div{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 20px 24px;
    border: white 2px solid;
    background-color: black;

}

.download_img{
    width: 14px;
}

.download_div:hover{
    background-color: white;
    color: black;
    cursor: pointer;
}

